@import url('https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600&display=swap');

:root {
  /* Light theme variables */
  --bg-color-light: #ffffff;
  --text-color-light: #1a1a1a;
  --card-bg-light: #f7f7f7;
  --border-color-light: #e0e0e0;
  --hover-color-light: #f0f0f0;
  --shadow-color-light: rgba(0, 0, 0, 0.05);
  
  /* Dark theme variables */
  --bg-color-dark: #000000;
  --text-color-dark: #ffffff;
  --card-bg-dark: #1c1c1e;
  --border-color-dark: #2c2c2e;
  --hover-color-dark: #2c2c2e;
  --shadow-color-dark: rgba(255, 255, 255, 0.05);
  
  /* Common variables */
  --transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  --profile-image: url('./assets/profile.jpg');  /* Default fallback path */

  /* Add RGB values for background colors */
  --bg-color-light-rgb: 255, 255, 255;
  --bg-color-dark-rgb: 0, 0, 0;

  /* Add RGB variables for text color */
  --text-color-light-rgb: 26, 26, 26;
  --text-color-dark-rgb: 255, 255, 255;
}

/* Default light theme */
:root[data-theme="light"] {
  --bg-color: var(--bg-color-light);
  --text-color: var(--text-color-light);
  --card-bg: var(--card-bg-light);
  --border-color: var(--border-color-light);
  --hover-color: var(--hover-color-light);
  --shadow-color: var(--shadow-color-light);
  --bg-color-rgb: var(--bg-color-light-rgb);
  --text-color-rgb: var(--text-color-light-rgb);
}

/* Dark theme */
:root[data-theme="dark"] {
  --bg-color: var(--bg-color-dark);
  --text-color: var(--text-color-dark);
  --card-bg: var(--card-bg-dark);
  --border-color: var(--border-color-dark);
  --hover-color: var(--hover-color-dark);
  --shadow-color: var(--shadow-color-dark);
  --bg-color-rgb: var(--bg-color-dark-rgb);
  --text-color-rgb: var(--text-color-dark-rgb);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, sans-serif;
  background-color: var(--bg-color);
  color: var(--text-color);
  line-height: 1.6;
  -webkit-font-smoothing: antialiased;
  transition: var(--transition);
}

body::before {
  content: '';
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: var(--profile-image);
  background-size: 100% auto;  /* Fill width, maintain aspect ratio */
  background-position: top center;  /* Align to top */
  background-repeat: no-repeat;
  opacity: 0.04;  /* Slightly reduced opacity */
  z-index: -1;
  pointer-events: none;
}

.container {
  max-width: none;
  margin: 0;
  padding: 0;
  position: relative;
  z-index: 1;
}

.header {
  text-align: center;
  margin-bottom: 2rem;
  padding: 3rem 0;
  position: relative;
  z-index: 1;
  background: rgba(var(--bg-color-rgb), 0.5);
  backdrop-filter: blur(8px);
  border: none;
  border-radius: 0;
  margin: 0;
  max-width: none;
}

.nav-buttons {
  position: fixed;
  top: 2rem;
  right: 2rem;
  display: flex;
  gap: 1rem;
  z-index: 1004;
}

.theme-toggle,
.game-toggle {
  background: var(--card-bg);
  border: 1px solid var(--border-color);
  padding: 1rem;
  border-radius: 50%;
  cursor: pointer;
  transition: var(--transition);
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0 4px 12px var(--shadow-color);
  width: 3.5rem;
  height: 3.5rem;
  z-index: 1004;
}

.theme-toggle:hover,
.game-toggle:hover {
  transform: scale(1.1);
}

/* Inverted colors for active states */
.theme-toggle[data-active="true"],
.game-toggle[data-active="true"] {
  background: var(--text-color);
  color: var(--bg-color);
}

.name {
  font-size: 3.5rem;
  font-weight: 600;
  margin-bottom: 1.5rem;
  letter-spacing: -0.5px;
  background: linear-gradient(to right, var(--text-color) 0%, #666 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.title {
  font-size: 1.25rem;
  font-weight: 400;
  color: #666;
  max-width: 600px;
  margin: 0 auto;
  line-height: 1.8;
}

.kanban {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  gap: 2rem;
  position: relative;
  background: rgba(var(--bg-color-rgb), 0.5);
  backdrop-filter: blur(8px);
  border-radius: 0;
  padding: 3rem 2rem;
  margin: 0;
}

.column {
  background: transparent;
  border-radius: 0;
  padding: 1.5rem;
  position: relative;
}

.column-header {
  font-size: 0.875rem;
  text-transform: uppercase;
  letter-spacing: 1px;
  margin-bottom: 1.5rem;
  color: #666;
  font-weight: 600;
}

.card {
  background: rgba(var(--bg-color-rgb), 0.7);
  backdrop-filter: blur(8px);
  border: 1px solid rgba(var(--text-color-rgb), 0.1);
  border-radius: 12px;
  padding: 1.5rem;
  margin-bottom: 1rem;
  cursor: pointer;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.card:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 24px var(--shadow-color);
}

.expanded-card-container {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1001;
  background: rgba(0, 0, 0, 0.4);
  backdrop-filter: blur(4px);
}

.card.expanded {
  position: relative;
  width: 90%;
  max-width: 800px;
  max-height: 80vh;
  overflow-y: auto;
  background: var(--bg-color);
  padding: 2rem;
  border-radius: 16px;
  box-shadow: 0 20px 50px rgba(0, 0, 0, 0.3);
  margin: 2rem;
  cursor: default;
  pointer-events: auto;
  animation: cardExpand 0.3s ease;
  transform: none;
  border: 1px solid var(--border-color);
}

.card.expanded .card-content {
  max-height: none;
  opacity: 1;
  margin-top: 1rem;
  pointer-events: auto;
}

.card.expanded .card-title {
  font-size: 1.5rem;
  margin-bottom: 1rem;
  color: var(--text-color);
}

.card.expanded .card-period {
  font-size: 1rem;
  color: var(--text-color);
  opacity: 0.8;
  margin-bottom: 1.5rem;
}

.card.expanded .card-content p {
  font-size: 1.1rem;
  line-height: 1.6;
  color: var(--text-color);
  margin-bottom: 1.5rem;
}

.card.expanded .tags {
  margin-top: 1.5rem;
}

.card.expanded .contact-link {
  margin-top: 1.5rem;
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
}

.card.expanded .github-link {
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  margin-top: 1.5rem;
  padding: 0.75rem 1.5rem;
  background: var(--text-color);
  color: var(--bg-color);
  border-radius: 8px;
  text-decoration: none;
  transition: transform 0.2s ease, box-shadow 0.2s ease;
  cursor: pointer;
}

.card.expanded .github-link:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 12px var(--shadow-color);
}

.card.expanded .github-link svg {
  width: 1.25rem;
  height: 1.25rem;
}

@keyframes cardExpand {
  0% {
    opacity: 0;
    transform: scale(0.95);
  }
  100% {
    opacity: 1;
    transform: scale(1);
  }
}

.card-title {
  font-size: 1.125rem;
  font-weight: 500;
  margin-bottom: 0.5rem;
}

.card-period {
  font-size: 0.875rem;
  color: #666;
  margin-bottom: 1rem;
}

.card-content {
  max-height: 0;
  opacity: 0;
  transition: opacity 0.3s ease, max-height 0.3s ease;
  overflow: hidden;
}

.card.expanded .card-content {
  max-height: none;
  opacity: 1;
  margin-top: 1rem;
  pointer-events: auto;
}

.tags {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
  margin-top: 1rem;
}

.tag {
  font-size: 0.75rem;
  padding: 0.25rem 0.75rem;
  background: var(--bg-color);
  border: 1px solid var(--border-color);
  border-radius: 20px;
  color: #666;
  transition: var(--transition);
}

.tag:hover {
  background: var(--hover-color);
}

.contact-bar {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(var(--bg-color-rgb), 0.8);
  backdrop-filter: blur(12px);
  padding: 1.5rem;
  display: flex;
  justify-content: center;
  gap: 2rem;
  border-top: 1px solid rgba(var(--text-color-rgb), 0.1);
  z-index: 1002;
}

.contact-link {
  color: var(--text-color);
  text-decoration: none;
  font-size: 0.875rem;
  transition: var(--transition);
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.5rem 1rem;
  border-radius: 20px;
  background: var(--card-bg);
  border: 1px solid var(--border-color);
}

.contact-link:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 12px var(--shadow-color);
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.4);
  opacity: 0;
  pointer-events: none;
  transition: opacity 0.3s ease;
  z-index: 1000;
}

.overlay.active {
  opacity: 1;
  pointer-events: auto;
}

/* Email contact section */
.email-contact {
  text-align: center;
  padding: 3rem 1rem;
  margin-bottom: 5rem;
  position: relative;
  background: rgba(var(--bg-color-rgb), 0.5);
  backdrop-filter: blur(8px);
  border-radius: 0;
  margin: 0;
  max-width: none;
}

.email-contact h2 {
  font-size: 2rem;
  font-weight: 500;
  margin-bottom: 1rem;
  background: linear-gradient(to right, var(--text-color) 0%, #666 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.email-contact p {
  font-size: 1.1rem;
  color: #666;
  margin-bottom: 2rem;
  max-width: 600px;
  margin-left: auto;
  margin-right: auto;
}

.email-form {
  max-width: 600px;
  margin: 0 auto;
}

.email-form input,
.email-form textarea {
  width: 100%;
  padding: 1rem;
  margin-bottom: 1rem;
  border: 1px solid var(--border-color);
  border-radius: 8px;
  background: var(--card-bg);
  color: var(--text-color);
  font-family: inherit;
  font-size: 1rem;
  transition: var(--transition);
}

.email-form textarea {
  min-height: 150px;
  resize: vertical;
}

.email-form input:focus,
.email-form textarea:focus {
  outline: none;
  border-color: #666;
  box-shadow: 0 0 0 2px rgba(102, 102, 102, 0.1);
}

.email-form button {
  background: var(--text-color);
  color: var(--bg-color);
  border: none;
  padding: 1rem 2rem;
  border-radius: 8px;
  font-size: 1rem;
  font-weight: 500;
  cursor: pointer;
  transition: var(--transition);
}

.email-form button:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 12px var(--shadow-color);
}

@media (max-width: 768px) {
  .container {
    padding: 1rem;
  }
  
  .header {
    padding: 2rem 0;
    margin-bottom: 2rem;
  }
  
  .name {
    font-size: 2.5rem;
  }
  
  .kanban {
    grid-template-columns: 1fr;
  }
  
  .card.expanded {
    margin: 1rem -1rem;
    padding: 1.5rem;
  }
  
  .theme-toggle {
    top: 1rem;
    right: 1rem;
  }
}

/* Ensure content stays above overlay */
.header,
.theme-toggle,
.contact-bar {
  position: relative;
  z-index: 1002;
}

/* Make sure expanded card content is interactive */
.card-content {
  transition: opacity 0.3s ease, max-height 0.3s ease;
}

.card.expanded .card-content * {
  pointer-events: auto;
}

/* Prevent text selection while dragging */
.card:not(.expanded) {
  user-select: none;
}

/* Make text selectable in expanded state */
.card.expanded {
  user-select: text;
}

/* Ensure proper stacking in dark mode */
:root[data-theme="dark"] .card.expanded {
  background: var(--bg-color-dark);
}

:root[data-theme="dark"] .overlay.active {
  background: rgba(0, 0, 0, 0.7);
}

/* Remove game-related styles */
.game-mode,
.game-container,
.pong-paddle,
.pong-ball,
.game-over,
.game-buttons,
.game-button,
.pong-score,
.game-controls-hint,
.game-character,
.game-obstacle,
.game-floor,
.game-active {
  display: none;
}

/* Remove game mode animations */
.game-mode .card,
.game-mode .header,
.game-mode .email-contact,
.game-mode .column-header {
  animation: none;
}

/* Remove game toggle button styles */
.game-toggle {
  display: none;
}

.digital-portrait {
  display: none;
}

.digital-portrait-container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  overflow: hidden;
}

.digital-portrait-container canvas {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

/* Ensure content remains visible */
.container, .header, .kanban, .email-contact, .digital-portrait {
  position: relative;
  z-index: 1;
}

/* Add subtle section transitions */
.header::after,
.kanban::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 1px;
  background: linear-gradient(
    to right,
    transparent,
    rgba(var(--text-color-rgb), 0.1),
    transparent
  );
}
